@import "../../../node_modules/jeet/jeet";
@import "moduler/variables";
@import "moduler/patterns";

@import "partiell/header";

// @include debug($kleinblue);


html, body {
  padding: 0;
  margin: 0;
	font-family: sans-serif;
}

#titeln {
  position: absolute;
  top: 50%;
  left: 38.2vw;
  span {
    &.ett {
      font-family: serif;
      font-size: 1.618rem *3;
      font-weight: 800;
      text-shadow: 0 0 13px $base3;
      text-transform: uppercase;
    }
    &.andra {
      font-family: serif;
      font-size: 1.618rem *2;
      font-weight: 800;
      text-shadow: 0 0 13px $base3;
      text-align: right;
      text-transform: capitalize;
      color: $base01;
    }
  }


}

#site-content {
	min-height: 100%;
  min-height: 100vh;
  padding-top: 1.618rem *4.14;
  position: relative;
  z-index: 1;
  -webkit-box-shadow: $base03 0 7px 35px 0;
  -moz-box-shadow: $base03 0 7px 35px 0;
  box-shadow: $base03 0 7px 35px 0;
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
	color: $base02;
}

#site-footer {
  min-height: 20vw;
  background-size: cover;
  background-position: center;
	padding: 25px 0;
  font-size: 85%;
  background-color: $base03;
  position: fixed;
  z-index: -1;
  left: 0;
  right: 0;
  bottom: 0;
  text-align: center;
  text-transform: capitalize;
	color: $base2;
	.copyright {
		position: absolute;
		top: 90%;
		left: 61.8%;
		color: $base0;

	}
}

.vertical-rl {
	writing-mode: vertical-rl;
  color: $base0;
  display: inline-block;
  float: right;
}

#info {
	position: absolute;
	right: 0;
	top: 50%;
	writing-mode: vertical-rl;
  color: $base0;
  display: inline-block;
  float: right;
	font-style: italic;
	span {
		font-size: .618rem;
		color: $base0;
		a {
			color: $dutchorange;
			text-decoration: none;
		}
	}
}
