header {
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 10;
}

@supports (position: sticky) {
  body {
    padding-top: 0;
  }

  header {
    position: sticky;
  }

}

.navbar__trans {
  // background-image: url('../bilder/background0.jpeg');
  position: relative;
  height: 1.618rem*2;
  width: 100%;
  // margin-bottom: 1.618rem;
  transition: .5s ease;


  .nav__trans {
    margin: 0;
    padding: 0;
    display: inline-block;
    float: right;
    margin-right: 5%;

    .item__trans {
      list-style: none;
      display: inline-block;
      font-size: 18px;
      padding: 5px;
      font-weight: 300;
      line-height: 80px;
      margin-right: 20px;
      transition: 0.5s ease;



      .highlight__trans {
        display: inline-block;
        height: 40px;
        line-height: 30px;
        padding: 5px;
        // border-radius: 2px;
        transition: 0.5s ease;
      }

      a {
        text-decoration: none;
        font-weight: 800;
        color: $base02;
        // text-shadow: 0 0 .618rem $base3;
        text-shadow: 0 0 5px $base3;

        &:hover {
          color: $blue;
          text-decoration: underline;
        }
      }
    }
  }

  .logo__trans {
    float: left;
    display: inline-block;
    margin-top: .618rem;
    margin-left: .618rem;
    width: 1.618rem*4.14;
    height: 1.618rem*4.14;
    // background-color: firebrick;
    transition: 0.5s ease;

    img {
      // display: none;
      width: 100%;
      &:hover {
        transform:rotate(45deg);
        -ms-transform:rotate(45deg);
        -moz-transform:rotate(45deg);
        -webkit-transform:rotate(45deg);
        -o-transform:rotate(45deg);
      }
    }
  }

  .title__logo__trans {

    img {
      height: 1.618rem*1.618;
    }
  }
}

.scrolled__trans {
  transition: 0.5s ease;

  &.navbar__trans {
    height: 1.618rem*1.618;
    background-color: $ikeayellow;
    transition: 0.5s ease;

    .nav__trans {
      .item__trans {
        line-height: 40px;
        transition: 0.5s ease;

        .highlight__trans {
          display: inline-block;
          background-color: $blue;
          color: $ikeayellow;
          height: 4rem*0.618;
          // line-height: 30px;
          padding: .618rem;
          text-shadow: none;
          // border-radius: 2em 1em 4em / 0.5em 3em;
          border-radius:  .618rem .618rem 0 0;
          transition: 0.5s ease;
        }
      }
    }
  }
}
