// Colors
$base03:    #002b36;
$base02:    #073642;
$base01:    #586e75;
$base00:    #657b83;
$base0:     #839496;
$base1:     #93a1a1;
$base2:     #eee8d5;
$base3:     #fdf6e3; //253,246,227
$yellow:    #b58900;
$orange:    #cb4b16;
$red:       #dc322f;
$magenta:   #d33682;
$violet:    #6c71c4;
$blue:      #268bd2;
$cyan:      #2aa198;
$green:     #859900;

$dutchorange: #FF4F00;
$kleinblue: #002fa7;
$ikeayellow: #FFDA1A; // 255,218,26
$ikeablue: #005E9D; // 0,94,157
// $ikeablue: #0051BA; // 0,81,186
$orange1: #FF7F3B;

$text-color: $base03;
$h1-color: rgba($base03, .83);
$h2-color: rgba($base03, .618);
$h3-color: rgba($base03, .382);

// Font weights
$light: 100;
$regular: 400;
$bold: 600;

// Base Font
$base-font-family: sans-serif;
$base-font-weight: $regular;
$base-font-size: 13px;
$base-line-height: 1.4;

// Fixed Font
$fixed-font-family: monospace;
$fixed-font-size: 85%;
$fixed-line-height: $base-line-height;

// Headings
$header-font-weight: $bold;

// Breakpoints
$breakpoint-xs-width: 414px;
$breakpoint-sm-width: 600px;
$breakpoint-md-width: 800px;
$breakpoint-lg-width: 1025px;

$mobile-small: 320px;
$mobile-middle: 375px;
$mobile-large: 414px;
$tablet-small: 768px;
$tablet-large: 1024px;
$screen-small: 1280px;
$screen-large: 1440px;
